import React from 'react'

const ModalForm = ({message,yesAction,noAction}) => {
  return (
    <div className="fixed  inset-0 bg-gray-800 bg-opacity-75 flex justify-center items-center  ">
          <div className="bg-white w-max p-4 rounded-lg h-36 grid gap-6 ">
             <p className='font-semibold text-lg px-4'>{message}</p>
             <p className='w-full flex space-x-4'>
                <button className='w-full py-2 bg-red-500 border-2 border-red-500 hover:bg-red-400 text-white rounded-md' onClick={()=>yesAction()}>Yes</button>
                <button className='w-full py-2 bg-gray-300  rounded-md border-2 border-gray-300 hover:bg-gray-200 ' onClick={()=>{noAction()}}>No</button>
             </p>
        </div>
        </div>
  )
}

export default ModalForm